import { RSAA } from "redux-api-middleware";
import { API_ROOT } from "Core/constants";
import {
  GET_PRODUCT_LIST_REQUEST,
  GET_PRODUCT_LIST_SUCCESS,
  GET_PRODUCT_LIST_FAILURE,
  GET_PRODUCT_DETAILS_REQUEST,
  GET_PRODUCT_DETAILS_SUCCESS,
  GET_PRODUCT_DETAILS_FAILURE,
  EDIT_PRODUCT_REQUEST,
  EDIT_PRODUCT_SUCCESS,
  EDIT_PRODUCT_FAILURE,
  REMOVE_PRODUCT_REQUEST,
  REMOVE_PRODUCT_SUCCESS,
  REMOVE_PRODUCT_FAILURE,
  CREATE_PRODUCT_REQUEST,
  CREATE_PRODUCT_SUCCESS,
  CREATE_PRODUCT_FAILURE
} from "./constants";

export function getProductListAction(exhibition) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/product/list?exhibition_id=${exhibition}`,
      method: "GET",
      types: [
        GET_PRODUCT_LIST_REQUEST,
        GET_PRODUCT_LIST_SUCCESS,
        GET_PRODUCT_LIST_FAILURE
      ]
    }
  };
}

export function getProductDetailsAction(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/product/${id}/view`,
      method: "GET",
      body: JSON.stringify(),
      types: [
        GET_PRODUCT_DETAILS_REQUEST,
        GET_PRODUCT_DETAILS_SUCCESS,
        GET_PRODUCT_DETAILS_FAILURE
      ]
    }
  };
}

export function editProductAction(id, productObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/product/${id}/update`,
      method: "POST",
      body: JSON.stringify(productObject),
      types: [EDIT_PRODUCT_REQUEST, EDIT_PRODUCT_SUCCESS, EDIT_PRODUCT_FAILURE]
    }
  };
}

export function removeProductAction(id) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/product/${id}/delete`,
      method: "GET",
      body: JSON.stringify(),
      types: [
        REMOVE_PRODUCT_REQUEST,
        REMOVE_PRODUCT_SUCCESS,
        REMOVE_PRODUCT_FAILURE
      ]
    }
  };
}

export function createProductAction(productObject) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/product/create`,
      method: "POST",
      body: JSON.stringify(productObject),
      types: [
        CREATE_PRODUCT_REQUEST,
        CREATE_PRODUCT_SUCCESS,
        CREATE_PRODUCT_FAILURE
      ]
    }
  };
}

export function uploadImage(file) {
  return {
    [RSAA]: {
      endpoint: `${API_ROOT}/seller/image/create`,
      method: "POST",
      body: JSON.stringify(file),
      types: [
        { type: CREATE_PRODUCT_REQUEST, meta: { showLoader: true } },
        { type: CREATE_PRODUCT_SUCCESS, meta: { showLoader: false } },
        { type: CREATE_PRODUCT_FAILURE, meta: { showLoader: false } }        
      ]
    }
  };
}
